<template>
  <div class="training">
    <h1>Math Training: Level {{level + 1}}</h1>
    <hr>
    <div class="progress">
      <div class="progress-bar" :style="progressStyles"></div>
    </div>
    <div class="box">
      <transition name="flip" mode="out-in">

        <app-start-screen v-if="state == 'start'"
                          @onStart="onStart"
        ></app-start-screen>
        <app-question v-else-if="state == 'question'"
                      @success="onQuestionSuccess"
                      @error="onQuestionError"
                      :settings="levels[level]"
        ></app-question>
        <app-message v-else-if="state == 'message'"
                     :type="message.type"
                     :text="message.text"
                     @onNext="onNext"
        ></app-message>
        <app-result-screen v-else-if="state == 'result'"
                           :stats="stats"
                           @repeat="onStart"
                           @nextLevel="onNextLevel"
        ></app-result-screen>
        <div v-else>Unknown state</div>
      </transition>
    </div>
  </div>
</template>

<script>

  export default {
    data () {
      return {
        state: 'start',
        stats: {
          success: 0,
          error: 0
        },
        message: {
          type: '',
          text: ''
        },
        questionMax: 3,
        level: 0,
        levels: [
          {
            from: 10,
            to: 40,
            range: 5,
            variants: 2
          },
          {
            from: 100,
            to: 200,
            range: 20,
            variants: 4
          },
          {
            from: 500,
            to: 900,
            range: 40,
            variants: 6
          }
        ]
      }
    },
    computed: {
      questionDone(){
        return this.stats.success + this.stats.error
      },
      progressStyles(){
        return {
          width: (this.questionDone / this.questionMax * 100) + '%'
        }
      }
    },
    methods: {
      onStart(){
        this.state = 'question'
        this.stats.success = 0
        this.stats.error = 0
      },
      onQuestionSuccess(){
        this.state = 'message';
        this.message.text = 'Good job!'
        this.message.type = 'success'
        this.stats.success++
      },
      onQuestionError(msg){
        this.state = 'message'
        this.message.text = msg
        this.message.type = 'warning'
        this.stats.error++
      },
      onNext(){
        if (this.questionDone < this.questionMax){
          this.state = 'question'
        }
        else {
          this.state = 'result'
        }
      },
      onNextLevel(){
        this.level++
        this.onStart()
      }
    }
  }
</script>

<style scoped>
  .training{
    max-width: 700px;
    margin: 20px auto;
  }

  .box {
    margin: 10px 0;
  }
  .progress-bar{
    transition: 0.5s;
  }

  .flip-enter{

  }
  .flip-enter-active{
    animation: flipInX 0.2s linear;
  }
  .flip-leave{

  }
  .flip-leave-active{
    animation: flipoutX 0.2s linear;
  }

  @keyframes flipInX {
    from{transform: rotateX(90deg);}
    to{transform: rotateX(0deg);}
  }

  @keyframes flipOutX {
    from{transform: rotateX(0deg);}
    to{transform: rotateX(90deg);}
  }

  @media only screen and (max-width: 650px) {
    .progress{
      padding: 8px;
      margin: 8px;
    }
  }


</style>
